import {
  Component,
  OnInit,
  OnDestroy,
  Input,
  Inject,
  Renderer2,
  ViewChild,
  ElementRef,
  AfterViewInit,
  HostBinding
} from '@angular/core';
import { AbstractControl } from '@angular/forms';
import { MYSCRIPT } from '@core/services';
import { APP_CONSTANTS } from '@app/app.constant';
import { AppConstant } from '@core/models/app-constant';
import { trigger, state, style, transition, animate } from '@angular/animations';

@Component({
  selector: 'app-my-script',
  templateUrl: './my-script.component.html',
  styleUrls: ['./my-script.component.scss'],
  animations: [
    trigger('slideContent', [
      state('void', style({ transform: 'translate3d(0, 25%, 0)', opacity: 0 })),
      state('enter', style({ transform: 'none', opacity: 1 })),
      state('leave', style({ transform: 'translate3d(0, 25%, 0)', opacity: 0 })),
      transition('* => *', animate('400ms cubic-bezier(0.25, 0.8, 0.25, 1)')),
    ])
  ]
})
export class MyScriptComponent implements AfterViewInit, OnDestroy {

  @Input()
  formControl: AbstractControl;

  @Input()
  width: number;

  @Input()
  height: number;

  @ViewChild('writerRef')
  writerRef: ElementRef;

  @HostBinding('@slideContent')
  public animateWriter = true;

  editor;

  isVirgin = true;

  constructor(
    private readonly renderer2: Renderer2,
    @Inject(MYSCRIPT)
    private readonly myScript,
    @Inject(APP_CONSTANTS)
    private readonly appConstants: AppConstant,
  ) {}


  ngAfterViewInit() {
    this.editor = this.myScript.register(this.writerRef.nativeElement, {
      recognitionParams: {
        type: 'TEXT',
        protocol: 'WEBSOCKET',
        apiVersion: 'V4',
        server: this.appConstants.MY_SCRIPT
      }
    });
    this.attachOnChangedListener();
  }

  attachOnChangedListener() {
    this.renderer2.listen(this.writerRef.nativeElement, 'exported', event => {

      if (this.formControl && !this.isVirgin) {
        const _exports = event.detail.exports;
        this.formControl.patchValue(_exports && !this.isVirgin ? _exports['text/plain'] : '');
      }

      this.isVirgin = false;
    });
  }

  ngOnDestroy() {
    this.editor
      .recognizer
      .close(this.editor.recognizerContext, this.editor.model, () => {});
  }

}
