import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import { HttpClient } from '@angular/common/http';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';

@Component({
  selector: 'app-finger-print',
  templateUrl: './finger-print.component.html',
  styleUrls: ['./finger-print.component.scss']
})
export class FingerPrintComponent implements OnInit {
  private userSession: UserSession;
  public leftFingerImg: any;
  public rightFingerImg: any;
  public leftFingerValue = 'THUMB';
  public rightFingerValue = 'THUMB';
  public searchFingerValue = 'THUMB';
  public searchImg: any;
  public fingerIndicator = 'LEFT';
  public placedFinger: string;
  public searchFingerData = [];
  public fingersData = [];
  public isLeftFinger = false;
  public isRightFinger = false;
  public isPleaceFinger = false;
  public isSearch = false;
  public leftLoadSuccess = false;
  public leftLoadError = false;
  public rightLoadSuccess = false;
  public rightLoadError = false;
  public isSearchingFinger = false;
  public isSearchSuccess = false;
  public isSearchError = false;
  public isEdit = false;
  public isFinger = false;
  public leftBiometricsOrientationId: any;
  public rightBiometricsOrientationId: any;
  constructor(
    public dialogRef: MatDialogRef<FingerPrintComponent>,
    private http: HttpClient,
    private snackBar: MatSnackBar,
    private userService: UserService,
    @Inject(MAT_DIALOG_DATA) public data
  ) {
    this.userSession = this.userService.getUser();
  }

  public fingerPrintsArray = [
    {
      biometricsOrientationId: null,
      fingerType: null,
      fingerprint: null,
      leftRightIndicator: null,
      createdUsrId: null,
      orgGrpId: null,
      orgId: null,
      modifiedUsrId: null,
      isFingerPrintAdded: false
    },
    {
      biometricsOrientationId: null,
      fingerType: null,
      fingerprint: null,
      leftRightIndicator: null,
      createdUsrId: null,
      orgGrpId: null,
      orgId: null,
      modifiedUsrId: null,
      isFingerPrintAdded: false
    }
  ];
  ngOnInit() {
    this.isSearch = this.data.isSearch;
    this.fingersData = this.data.data;
    this.isEdit = this.data.isEdit;
    // this code for edit functionality while editing it is enable the captured finger.
    if (this.fingersData.length) {
      this.fingersData.forEach(ele => {
        if (ele.leftRightIndicator === 'LEFT') {
          this.leftFingerValue = ele.fingerType;
          this.leftBiometricsOrientationId = ele['biometricsOrientationId'];
          this.fingerPrintsArray[0].biometricsOrientationId = ele['biometricsOrientationId'];
          this.fingerPrintsArray[0].fingerType = this.leftFingerValue;
          this.fingerPrintsArray[0].fingerprint = ele.fingerprint;
          this.fingerPrintsArray[0].leftRightIndicator = 'LEFT';
          this.fingerPrintsArray[0].createdUsrId = this.userSession.regId;
          this.fingerPrintsArray[0].orgGrpId = this.userSession.orgGroupId;
          this.fingerPrintsArray[0].orgId = this.userSession.orgId;
          this.fingerPrintsArray[0].modifiedUsrId = this.userSession.regId;
          if (ele.fingerprint) {
            this.isLeftFinger = true;
            this.leftLoadSuccess = true;
            this.leftLoadError = false;
          }
        } else if (ele.leftRightIndicator === 'RIGHT') {
          this.rightFingerValue = ele.fingerType;
          this.rightBiometricsOrientationId = ele['biometricsOrientationId'];
          this.fingerPrintsArray[1].biometricsOrientationId = ele['biometricsOrientationId'];
          this.fingerPrintsArray[1].fingerType = this.rightFingerValue;
          this.fingerPrintsArray[1].fingerprint = ele.fingerprint;
          this.fingerPrintsArray[1].leftRightIndicator = 'RIGHT';
          this.fingerPrintsArray[1].createdUsrId = this.userSession.regId;
          this.fingerPrintsArray[1].orgGrpId = this.userSession.orgGroupId;
          this.fingerPrintsArray[1].orgId = this.userSession.orgId;
          this.fingerPrintsArray[1].modifiedUsrId = this.userSession.regId;
          if (ele.fingerprint) {
            this.isRightFinger = true;
            this.rightLoadSuccess = true;
            this.rightLoadError = false;
          }
        }
      });
    }
  }

  /**
   * @description this method is used for captur the finger print and store it in object by checking it is left or roght.
   * @function captureFinger
   * @param data
   */
  captureFinger(data) {
    switch (data) {
      case 'left':
        this.placedFinger = 'LEFT ' + this.leftFingerValue;
        this.isLeftFinger = true;
        break;
      case 'right':
        this.placedFinger = 'RIGHT ' + this.rightFingerValue;
        this.isRightFinger = true;
        break;
      case 'search':
        this.isSearchingFinger = true;
    }
    this.isPleaceFinger = true;
    const url = 'https://localhost:15005/CaptureFingerprint';
    this.http.post(url + '?5&1', true).subscribe(
      response => {
        if (response && response['ReturnCode'] === '0') {
          const fingerImg = response['Base64BMPIMage'];
            switch (data) {
              case 'left':
                this.fingerPrintsArray[0].biometricsOrientationId = this.leftBiometricsOrientationId,
                this.fingerPrintsArray[0].fingerType = this.leftFingerValue;
                this.fingerPrintsArray[0].fingerprint = fingerImg;
                this.fingerPrintsArray[0].leftRightIndicator = 'LEFT';
                this.fingerPrintsArray[0].createdUsrId = this.userSession.regId;
                this.fingerPrintsArray[0].orgGrpId = this.userSession.orgGroupId;
                this.fingerPrintsArray[0].orgId = this.userSession.orgId;
                this.fingerPrintsArray[0].modifiedUsrId = this.userSession.regId;
                this.fingerPrintsArray[0].isFingerPrintAdded = true;
                this.leftLoadSuccess = true;
                this.leftLoadError = false;
                break;
              case 'right':
                this.fingerPrintsArray[1].biometricsOrientationId = this.rightBiometricsOrientationId,
                this.fingerPrintsArray[1].fingerType = this.rightFingerValue;
                this.fingerPrintsArray[1].fingerprint = fingerImg;
                this.fingerPrintsArray[1].leftRightIndicator = 'RIGHT';
                this.fingerPrintsArray[1].createdUsrId = this.userSession.regId;
                this.fingerPrintsArray[1].orgGrpId = this.userSession.orgGroupId;
                this.fingerPrintsArray[1].orgId = this.userSession.orgId;
                this.fingerPrintsArray[1].modifiedUsrId = this.userSession.regId;
                this.fingerPrintsArray[1].isFingerPrintAdded = true;
                this.rightLoadSuccess = true;
                this.rightLoadError = false;
                break;
              case 'search':
                this.searchImg = 'data:image;base64,' + fingerImg;
                const searchObj = {
                  fingerType: this.searchFingerValue,
                  fingerprint: fingerImg,
                  leftRightIndicator: this.fingerIndicator
                };
                this.searchFingerData.push(searchObj);
                this.isSearchError = false;
                this.isSearchSuccess = true;
                this.isFinger = true;
                break;
            }
            this.fingersData = this.fingerPrintsArray;
        } else if (response && response['ReturnCode'] === '-19') {
          switch (data) {
            case 'left':
              this.leftLoadError = true;
              break;
            case 'right':
              this.rightLoadError = true;
              break;
            case 'search':
              this.isSearchError = true;
              this.isFinger = false;
              break;
          }
          this.snackBar.open('Please place your finger properly', 'Cancel', {
            duration: 5000
          });
        } else if (response && response['ReturnCode'] === '-42') {
          switch (data) {
            case 'left':
              this.leftLoadError = true;
              break;
            case 'right':
              this.rightLoadError = true;
              break;
            case 'search':
              this.isSearchError = true;
              this.isFinger = false;
              break;
          }
          this.snackBar.open('Please Check your finger print device connected or not', 'Cancel', {
            duration: 5000
          });
        }
        this.isPleaceFinger = false;
      },
      () => {
        switch (data) {
          case 'left':
            this.leftLoadError = true;
            break;
          case 'right':
            this.rightLoadError = true;
            break;
          case 'search':
            this.isSearchError = true;
            this.isFinger = false;
            break;
        }
        this.snackBar.open(
          'Please check your finger print service is available/running or not',
          'Cancel',
          {
            duration: 5000
          }
        );
      }
    );
  }

  /**
   * @description this method is used for close the model.
   * @function cancel
   */
  cancel() {
    this.dialogRef.close();
  }

  /**
   * @description this method is used for delete the apture finger whether left or right.
   * @function cancel
   * @param data
   */
  deleteFinger(data) {
    if (data === 'LEFT') {
      this.fingerPrintsArray[0].biometricsOrientationId = this.leftBiometricsOrientationId;
      this.fingerPrintsArray[0].createdUsrId = null;
      this.fingerPrintsArray[0].fingerType = this.leftFingerValue;
      this.fingerPrintsArray[0].fingerprint = null;
      this.fingerPrintsArray[0].leftRightIndicator = 'LEFT';
      this.fingerPrintsArray[0].modifiedUsrId = this.userSession.regId;
      this.fingerPrintsArray[0].orgGrpId = '';
      this.fingerPrintsArray[0].orgId = '';
      this.fingerPrintsArray[0].isFingerPrintAdded = true;
      this.isLeftFinger = false;
      this.leftLoadError = false;
      this.leftLoadSuccess = false;
    } else {
      this.fingerPrintsArray[1].biometricsOrientationId = this.rightBiometricsOrientationId;
      this.fingerPrintsArray[1].createdUsrId = null;
      this.fingerPrintsArray[1].fingerType = this.rightFingerValue;
      this.fingerPrintsArray[1].fingerprint = null;
      this.fingerPrintsArray[1].leftRightIndicator = 'RIGHT';
      this.fingerPrintsArray[1].modifiedUsrId = this.userSession.regId;
      this.fingerPrintsArray[1].orgGrpId = '';
      this.fingerPrintsArray[1].orgId = '';
      this.fingerPrintsArray[1].isFingerPrintAdded = true;
      this.isRightFinger = false;
      this.rightLoadError = false;
      this.rightLoadSuccess = false;
    }
    this.fingersData = this.fingerPrintsArray;
  }
}
